<template>
  <h1 style="text-align:center;color:rgb(6,90,92)">카운트다운 시간 설정하기</h1>
  <div class="gridContainer">
    <button ref="addHourDecimal" @click="addHourDecimal" class="btn">^</button>
    <button ref="addHourUnit" @click="addhourPart" class="btn">^</button>
    <div></div>
    <button ref="addMinDecimal" @click="addMinDecimal" class="btn">^</button>
    <button ref="addMinUnit" @click="addminPart" class="btn">^</button>
    <div></div>
    <button ref="addSecDecimal" @click="addSecDecimal" class="btn">^</button>
    <button ref="addSecUnit" @click="addsecPart" class="btn">^</button>
    <input ref="hourInput" size="2" maxlength="2" v-model="hourPart" class="twoColElem spaceSet" @input="checkInput" />
    <span class="columnDots">&nbsp;:</span>
    <input ref="minInput" size="2" maxlength="2" v-model="minPart" class="twoColElem spaceSet" @input="checkInput" />
    <span class="columnDots">&nbsp;:</span>
    <input ref="secInput" type="tel" size="2" maxlength="2" v-model="secPart" class="twoColElem spaceSet"
      @input="checkInput" />

    <button ref="subHourDecimal" @click="subHourDecimal" class="btn">v</button>
    <button ref="subHourUnit" @click="subhourPart" class="btn">v</button>
    <div></div>
    <button ref="subMinDecimal" @click="subMinDecimal" class="btn">v</button>
    <button ref="subMinUnit" @click="subminPart" class="btn">v</button>
    <div></div>
    <button ref="subSecDecimal" @click="subSecDecimal" class="btn">v</button>
    <button ref="subSecUnit" @click="subSecPart" class="btn">v</button>
  </div>
  <div class="timer">
    <button @click="startCountDown" class="btn btn-success">카운트다운 시작</button>
    <button @click="stopTimer" class="btn btn-success">카운트다운 정지</button>
    <button @click="resetTimer" ref="resetTimerBtn" class="btn btn-success">카운트다운 리셋</button>

  </div>

</template>
<script>
export default {
  data() {
    return {
      secPart: "00",
      minPart: "00",
      hourPart: "00",
      SettedTimer: new Date(),
      intervalClock: null,
    };
  },
  methods: {

    resetTimer() {

      //reset visuals
      let theComp = this;
      theComp.secPart = theComp.update(0);
      theComp.minPart = theComp.update(0);
      theComp.hourPart = theComp.update(0);

      //reset the date obj
      let theSettedTimer = this.SettedTimer
      theSettedTimer.setHours(0)
      theSettedTimer.setMinutes(0)
      theSettedTimer.setSeconds(0)
    },
    stopTimer() {
      this.EnableInputs()
      clearInterval(this.intervalClock)
      let sec = '' + this.update(this.SettedTimer.getSeconds())

      let min = '' + this.update(this.SettedTimer.getMinutes())

      let hou = '' + this.update(this.SettedTimer.getHours())

      // this.SettedTimer.setSeconds(sec)
      // this.SettedTimer.setMinutes(min)
      // this.SettedTimer.setHours(hou)  
      this.secPart = sec
      this.minPart = min
      this.hourPart = hou
    },
    checkInput(e) {
      let ch = String.fromCharCode(e.which);
      if (!(/[0-9]/.test(ch))) {
        e.preventDefault();
      }
      let theComp = this;

      if (theComp.secPart >= 60) { theComp.secPart = "59" }
      if (theComp.minPart >= 60) { theComp.minPart = "59" }
      if (theComp.hourPart > 24) { theComp.hourPart = "23" }

    },
    update(k) {
      if (k < 10) {
        return "0" + k;
      } else {
        return k;
      }
    },
    EnableInputs() {
      this.$refs.subSecUnit.disabled = false
      this.$refs.subSecDecimal.disabled = false
      this.$refs.subMinUnit.disabled = false
      this.$refs.subMinDecimal.disabled = false
      this.$refs.subHourUnit.disabled = false
      this.$refs.subHourDecimal.disabled = false
      this.$refs.addSecUnit.disabled = false
      this.$refs.addSecDecimal.disabled = false
      this.$refs.addMinUnit.disabled = false
      this.$refs.addMinDecimal.disabled = false
      this.$refs.addHourUnit.disabled = false
      this.$refs.addHourDecimal.disabled = false

      this.$refs.hourInput.disabled = false
      this.$refs.minInput.disabled = false
      this.$refs.secInput.disabled = false

      this.$refs.resetTimerBtn.disabled = false

    },
    DisableInputs() {

      this.$refs.subSecUnit.disabled = true
      this.$refs.subSecDecimal.disabled = true
      this.$refs.subMinUnit.disabled = true
      this.$refs.subMinDecimal.disabled = true
      this.$refs.subHourUnit.disabled = true
      this.$refs.subHourDecimal.disabled = true
      this.$refs.addSecUnit.disabled = true
      this.$refs.addSecDecimal.disabled = true
      this.$refs.addMinUnit.disabled = true
      this.$refs.addMinDecimal.disabled = true
      this.$refs.addHourUnit.disabled = true
      this.$refs.addHourDecimal.disabled = true

      this.$refs.hourInput.disabled = true
      this.$refs.minInput.disabled = true
      this.$refs.secInput.disabled = true

      this.$refs.resetTimerBtn.disabled = true
    },
    startCountDown() {
      let theComp = this;
      let theSettedTimer = this.SettedTimer

      theSettedTimer.setHours(this.hourPart)
      theSettedTimer.setMinutes(this.minPart)
      theSettedTimer.setSeconds(this.secPart)

      this.intervalClock = setInterval(function () {
        theComp.DisableInputs();
        theSettedTimer.setSeconds(theSettedTimer.getSeconds() - 1)
        theComp.secPart = theComp.update(theSettedTimer.getSeconds());
        theComp.minPart = theComp.update(theSettedTimer.getMinutes());
        theComp.hourPart = theComp.update(theSettedTimer.getHours());

        if (theSettedTimer.getSeconds() == 0 && theSettedTimer.getMinutes() == 0 && theSettedTimer.getHours() == 0) {
          theComp.stopTimer();
        }
      }, 1000);

    },
    addHourDecimal() {
      let tmpDecimal = this.hourPart[0];
      if (tmpDecimal >= 2) {
        let concat = "" + "2" + this.hourPart[1];
        this.hourPart = concat;
      } else {
        tmpDecimal = Number(tmpDecimal) + 1;
      let concat = "" + tmpDecimal + this.hourPart[1];
      this.hourPart = concat;
      }
    },
    addhourPart() {
      let tempHourWhole = this.hourPart;
      let tmpUnit = tempHourWhole[1];
        if (tmpUnit >=3 && tempHourWhole[0]>= 2) {
          const concat = "" + tempHourWhole[0] + "3";
          this.hourPart = concat;
      }else if (tmpUnit == 9) {
        this.addHourDecimal();
        let tempHourWhole = this.hourPart;
        const concat = "" + tempHourWhole[0] + 0;
        this.hourPart = concat;
      } else {
        tmpUnit = Number(tmpUnit) + 1;
        const concat = "" + tempHourWhole[0] + tmpUnit;
        this.hourPart = concat;
      }
    },
    addMinDecimal() {
      let tmpDecimal = this.minPart[0];
      if (tmpDecimal >= 5) {
        const concat = "" + 0 + this.minPart[1];
        this.minPart = concat;
      } else {
        tmpDecimal = Number(tmpDecimal) + 1;
        const concat = "" + tmpDecimal + this.minPart[1];
        this.minPart = concat;
      }
    },
    addminPart() {
      let tempMinWhole = this.minPart;
      let tmpUnit = tempMinWhole[1];
      if (tmpUnit == 9) {
        this.addMinDecimal();
        let tempMinWhole = this.minPart;
        const concat = "" + tempMinWhole[0] + 0;
        this.minPart = concat;
      } else {
        tmpUnit = Number(tmpUnit) + 1;
        const concat = "" + tempMinWhole[0] + tmpUnit;
        this.minPart = concat;
      }
    },
    addSecDecimal() {
      let tmpDecimal = this.secPart[0];
      if (tmpDecimal >= 5) {
        const concat = "" + 0 + this.secPart[1];
        this.secPart = concat;
      } else {
        tmpDecimal = Number(tmpDecimal) + 1;
        const concat = "" + tmpDecimal + this.secPart[1];
        this.secPart = concat; //set the component s display
      }
    },
    addsecPart() {
      let tempSecWhole = this.secPart;
      let tmpUnit = tempSecWhole[1];
      if (tmpUnit == 9) {
        this.addSecDecimal();
        let tempSecWhole = this.secPart;
        const concat = "" + tempSecWhole[0] + 0;
        this.secPart = concat;
      } else {
        tmpUnit = Number(tmpUnit) + 1;
        const concat = "" + tempSecWhole[0] + tmpUnit;
        this.secPart = concat;
      }
    },
    subHourDecimal() {
      let tempHourWhole1 = this.hourPart;
      let tmpDecimal = tempHourWhole1[0];
      tmpDecimal = Number(tmpDecimal) - 1;
      if (tmpDecimal < 0) {
        tmpDecimal = 0
      }
      const concat = "" + tmpDecimal + tempHourWhole1[1];
      this.hourPart = concat;
    },
    subhourPart() {
      let tempHourWhole2 = this.hourPart;
      let tmpUnit = tempHourWhole2[1];
      if (tempHourWhole2[1] == 0 && tempHourWhole2[0] == 0) {
        this.hourPart = "" + 0 + 0;
      }
      else if (tmpUnit == 0) {
        tmpUnit = 9
        this.hourPart = "" + (Number(tempHourWhole2[0]) - 1) + tmpUnit;
      }
      else {
        tmpUnit = Number(tmpUnit) - 1;
        this.hourPart = "" + tempHourWhole2[0] + tmpUnit;
      }
    },
    subMinDecimal() {
      let tempMinWhole1 = this.minPart;
      let tmpDecimal = tempMinWhole1[0];
      tmpDecimal = Number(tmpDecimal) - 1;
      if (tmpDecimal < 0) {
        tmpDecimal = 0
      }
      const concat = "" + tmpDecimal + tempMinWhole1[1];
      this.minPart = concat;
    },
    subminPart() {
      let tempMinWhole2 = this.minPart;
      let tmpUnit = tempMinWhole2[1];
      if (tempMinWhole2[1] == 0 && tempMinWhole2[0] == 0) {
        this.minPart = "" + 0 + 0;
      }
      else if (tmpUnit == 0) {
        tmpUnit = 9
        this.minPart = "" + (Number(tempMinWhole2[0]) - 1) + tmpUnit;
      }
      else {
        tmpUnit = Number(tmpUnit) - 1;
        this.minPart = "" + tempMinWhole2[0] + tmpUnit;
      }
    },
    subSecDecimal() {
      let tempSecWhole1 = this.secPart;
      let tmpDecimal = tempSecWhole1[0];
      tmpDecimal = Number(tmpDecimal) - 1;
      if (tmpDecimal < 0) {
        tmpDecimal = 0
      }
      const concat = "" + tmpDecimal + tempSecWhole1[1];
      this.secPart = concat;
    },
    subSecPart() {
      let tempSecWhole2 = this.secPart;
      let tmpUnit = tempSecWhole2[1];
      if (tempSecWhole2[1] == 0 && tempSecWhole2[0] == 0) {
        const concat = "" + 0 + 0;
        this.secPart = concat;
      }
      else if (tmpUnit == 0) {
        tmpUnit = 9
        const concat = "" + (Number(tempSecWhole2[0]) - 1) + tmpUnit;
        this.secPart = concat;
      }
      else {
        tmpUnit = Number(tmpUnit) - 1;
        const concat = "" + tempSecWhole2[0] + tmpUnit;
        this.secPart = concat;
      }
    },

  },
};
</script>

<style scoped>
body {
  background-color: aliceblue;
}

.columnDots {
  font-size: 40px;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.timer{
  text-align: center;
  grid-auto-columns: auto;
}
.btn-success{
  margin:1em;
}
/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}

.gridContainer {
  display: grid;
  grid-template-columns: auto auto auto auto auto auto auto auto;
  width: 600px;
  margin: 0 auto;
}

.twoColElem {
  grid-column: span 2;
  font-size: 20px;
  border-radius: 1em;
}

.spaceSet {
  /* text-align: center;  */
  letter-spacing: 40px;
  text-indent: 40px;
}
</style>